import React from "react";
import Logo from "../images/logo.svg";
import face from "../images/icon-facebook.svg";
import instagram from "../images/icon-instagram.svg";
import pinterest from "../images/icon-pinterest.svg";
import twitter from "../images/icon-twitter.svg";

const Footer = () => {
  return (
    <div className="footer-grid pb-4">
      <div className="pb-3">
        <img src={Logo} alt="" />
      </div>
      <div className="footer-icons">
        <a href="">
          <img className="pr-5" src={face} alt="" />
        </a>
        <a href="">
          <img className="pr-5" src={instagram} alt="" />
        </a>
        <a href="">
          <img className="pr-5" src={pinterest} alt="" />
        </a>
        <a href="">
          <img className="pr-5" src={twitter} alt="" />
        </a>
      </div>
      <div className="footer-menu">
        <h4 className="text-light pr-5">About</h4>
        <h4 className="text-light pr-5">Careers</h4>
        <h4 className="text-light pr-5">Events</h4>
        <h4 className="text-light pr-5">Products</h4>
      </div>

      <div>
        <h5 className="text-muted">2021.Loopstudios. All rights reserverd.</h5>
      </div>
    </div>
  );
};

export default Footer;
