import React from "react";
import curiosity from "../images/desktop/image-curiosity.jpg";
import deepEarth from "../images/desktop/image-deep-earth.jpg";
import fishEye from "../images/desktop/image-fisheye.jpg";
import fromAbove from "../images/desktop/image-from-above.jpg";
import grid from "../images/desktop/image-grid.jpg";
import nightArcade from "../images/desktop/image-night-arcade.jpg";
import pocketBorealis from "../images/desktop/image-pocket-borealis.jpg";
import soccerTeam from "../images/desktop/image-soccer-team.jpg";

const images = [
  { id: 1, src: curiosity, name: "curiosity" },
  { id: 2, src: deepEarth, name: "deepEarth" },
  { id: 3, src: fishEye, name: "fishEye" },
  { id: 4, src: fromAbove, name: "fromAbove" },
  { id: 5, src: grid, name: "grid" },
  { id: 6, src: nightArcade, name: "nightArcade" },
  { id: 7, src: pocketBorealis, name: "pocketBorealis" },
  { id: 8, src: soccerTeam, name: "soccerTeam" },
];

const CreationsImages = () => {
  return (
    <div className="creation-images-grid">
      {images.map((image) => (
        <div className={image.name} key={image.id} alt="imagen">
          <h2 className="text-light text-capitalize ">{image.name}</h2>
        </div>
      ))}
    </div>
  );
};

export default CreationsImages;
